.about_us{
    min-height: 100vh;
    background-color: bisque;
    padding-top: 4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about_us .title{
    margin-top: 0;
    color: rgb(165, 131, 88);
    margin-bottom: 3rem;
}

.about_us .content{
    color: rgb(97, 72, 38);
    font-size: 1.2rem;
    width: 80%;
    text-align: justify;
    line-height: 2rem;
}

.about_us .heading{
    color: rgb(97, 72, 38);
    font-size: 1.3rem;
    font-weight: 700;
    text-align: left;
    width: 80%;
}


@media screen and (max-width: 960px) {
    .about_us{
        padding-top: 1rem;
    }
}