.contact_us {
  /* background-image: linear-gradient(rgba(28, 28, 28, 0.8), rgba(0, 0, 0, 0.4)),
    url("../Assets/moose.jpg");
  background-size: cover; */
  background-color: rgb(37, 37, 37);
  min-height: 100vh;
  padding-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact_us .page_title {
  font-size: 4rem;
  color: rgb(224, 215, 210);
}

.contact_us .dialog {
  margin-top: 3rem;
  font-size: 1.7rem;
  color: rgb(208, 192, 183);
  width: 70%;
  line-height: 3rem;
  font-style: italic;
}

.contact_modules {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding-top: 5rem;
  padding-bottom: 9rem;
}

.contact_details {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  font-size: 1.3rem;
  font-weight: 600;
  color: rgb(233, 223, 217);
}

.contact_details div {
  display: flex;
  align-items: center;
}

.contact_modules img {
  height: 1.5rem;
  margin-right: 1rem;
}

.user_details {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.user_details input {
  width: 18rem;
  padding: 0.8rem;
  font-size: 1rem;
  background-color: rgb(202, 195, 195);
  border: 2px solid rgb(84, 67, 67);
  margin: 1rem;
  border-radius: 2rem;
}
.user_details input:hover {
  border: 2px solid rgb(255, 255, 255);
}

.submit_btn {
  margin-top: 2rem;
  background-color: rgb(249, 249, 0);
  width: 90%;
  padding: 0.8rem;
  font-size: 1.2rem;
  color: rgb(64, 57, 0);
  border-radius: 1rem;
  cursor: pointer;
  user-select: none;
  font-weight: 600;
}

.submit_btn:active {
  background-color: rgb(255, 255, 188);
}

@media screen and (max-width: 960px) {
  .contact_modules {
    flex-direction: column;
    padding-top: 2rem;
    padding-bottom: 5rem;
  }
  .contact_details div {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
  }
  .contact_details {
    margin-bottom: 3rem;
  }
  .submit_btn{
    width: 18rem;
  }
  .contact_us .dialog {
    width: 90%;
  }
}
